<template>
  <div>
    <div class="page-index" v-if="page_control.index">
      <div v-for="(item,key) in index_content">
        <section
          v-bind:class="{'white':key=='introduction','yallow':key=='tool','pink':key=='plan'}"
        >
          <h1>{{item.title}}</h1>
          <div class="group" v-bind:class="{'contrary':key=='tool'}">
            <div class="photo">
              <div class="myphoto" v-if="key=='introduction'"></div>
              <div class="photo-bg" v-if="key!='introduction'">
                <img v-if="key=='tool'" src="../assets/icon/logo.png" alt />
                <img v-if="key=='plan'" src="../assets/icon/plan.png" alt />
              </div>
            </div>
            <div class="description">
              <h2>{{item.description}}</h2>
              <p class="txt">
                {{item.txt}}
                <router-link to="/intro" class="more-btn" v-if="key=='introduction'">more</router-link>
                <router-link to="/tool" class="more-btn" v-if="key=='tool'">more</router-link>
                <router-link to="/plan" class="more-btn" v-if="key=='plan'">more</router-link>
              </p>
              <router-link to="/intro" class="more-btn-phone" v-if="key=='introduction'">more</router-link>
              <router-link to="/tool" class="more-btn-phone" v-if="key=='tool'">more</router-link>
              <router-link to="/plan" class="more-btn-phone" v-if="key=='plan'">more</router-link>
            </div>
          </div>
        </section>
      </div>
    </div>
    <div class="page-other" v-if="page_control.intro">
      <div v-for="item in introduction_content">
        <h1 class="title">{{item.title}}</h1>
        <p class="txt">{{item.txt}}</p>
      </div>
    </div>
    <div class="page-other" v-if="page_control.tool">
      <div v-for="item in tool_content">
        <h1 class="title">{{item.title}}</h1>
        <p class="txt">{{item.txt}}</p>
      </div>
    </div>
    <div class="page-other" v-if="page_control.plan">
      <div v-for="item in plan_content">
        <h1 class="title">{{item.title}}</h1>
        <p class="txt">{{item.txt}}</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["page"],
  data(page) {
    let now_page = page.$options.propsData.page;
    let page_control = {
      index: false,
      intro: false,
      tool: false,
      plan: false
    };
    switch (now_page) {
      case "index":
        page_control.index = true;
        break;
      case "intro":
        page_control.intro = true;
        break;
      case "tool":
        page_control.tool = true;
        break;
      case "plan":
        page_control.plan = true;
        break;
    }

    let index_content = {};
    let introduction_content = {};
    let tool_content = {};
    let plan_content = {};
    index_content = {
      introduction: {
        title: "Introduction",
        description: "coding",
        txt:
          ",,,"
      },
      tool: {
        title: "Tool",
        description: "",
        txt:
          ",,,,,"
      },
      plan: {
        title: "Plan",
        description: ",",
        txt:
          ",,...?"
      }
    };

    introduction_content = {
      myself: {
        title: "ABOUT ME",
        txt:
          ",,,,"
      },
      interest: {
        title: "LOVE OF OUTDOORS",
        txt:
          ",,,,,"
      },
      experience: {
        title: "EXPERIENCE",
        txt:
          ",,,,,,,,,,"
      }
    };

    tool_content = {
      basic: {
        title: "BASIC",
        txt:
          "HTMLCSSJavaScript , JQuery "
      },
      angular: {
        title: "Angular",
        txt:
          ", case  angular "
      },
      vue: {
        title: "Vue",
        txt:
          ",,,,Vue(Single Page Application SPA),"
      },
      laravel: {
        title: "Laravel",
        txt:
          "PHP ,, MySql , CRUD ,"
      }
    };
    plan_content = {
      plan: {
        title: "",
        txt:
          ",,,, side project , laravel , side project , side project ,"
      }
    };

    return {
      page_control,
      index_content,
      introduction_content,
      tool_content,
      plan_content
    };
  }
};
</script>

<style lang="scss">
.page-index {
  z-index: 0;

  section {
    // display: flex;
    width: 100%;
    height: 700px;
    padding: 150px 0;
    // border-bottom:1px solid black;

    h1 {
      width: 200px;
      height: 50px;
      border: 1px solid black;
      text-align: center;
      font-size: 35px;
      margin: 0 auto 20px;
    }

    .group {
      display: flex;

      .photo,
      .description {
        display: flex;
        width: 50%;

        // img {
        //   width: 300px;
        //   height: 300px;
        //   border-radius: 50%;
        // }

        .myphoto {
          width: 300px;
          height: 300px;
          background: url("../assets/imgs/myphoto.jpg") no-repeat;
          background-size: 100% 130%;
          border-radius: 50%;
        }

        .photo-bg {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 300px;
          height: 300px;
          background: #fff;
          border-radius: 50%;
        }

        h2 {
          margin: 0 0 20px 0;
          font-size: 35px;
        }

        .txt {
          z-index: 0;
          width: 450px;
          position: relative;
        }
      }

      .description {
        flex-direction: column;
        padding: 50px 0 0 0;
        text-align: left;
      }

      .photo {
        justify-content: flex-end;
        padding: 0 70px 0 0;
      }
    }

    .more-btn {
      position: absolute;
      bottom: -50px;
      left: 380px;
      width: 70px;
      border: 1px solid black;
      text-align: center;
      transition: all 0.2s ease-in-out;

      &:active {
        transform: scale(0.9);
      }
    }

    .more-btn-phone {
      display: none;
      width: 70vw;
      height: 10vw;
      font-size: 30px;
      margin: 20px auto 0;
      border: 1px solid black;
      text-align: center;
    }

    .contrary {
      flex-direction: row-reverse;

      .description {
        // justify-content: flex-end;
        text-align: right;
        flex-wrap: wrap-reverse;
      }

      .more-btn {
        left: 0;
      }

      .photo {
        padding: 0 50px;
        justify-content: flex-start;
      }
    }
  }

  @media screen and (max-width: 1000px) {
    section {
      padding: 20px 5px;

      .group {
        display: block;

        .photo {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 100vw;
          padding: 0;
        }

        .description {
          // display: flex;
          // justify-content: center;
          text-align: center;
          padding: 10px 0;
          width: 100vw;
          font-size: 20px;

          .txt {
            width: 80vw;
            margin: 0 auto;
            text-align: center;
            font-size: 20px;
          }

          h2 {
            font-size: 30px;
          }
        }
      }

      .more-btn {
        display: none;
      }
      .more-btn-phone {
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}

.page-other {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 50px;

  .title {
    font-size: 35px;
    text-align: center;
  }

  .txt {
    width: 50vw;
    margin: 0 auto 20px;
    text-align: center;
    line-height: 40px;
  }

  @media screen and (max-width: 1000px) {
    .txt {
      width: 70vw;
      font-size: 15px;
      line-height: 20px;
    }
  }
}
</style>

